export default {
	
	data() {
		return {
			tabCurrentIndex: 0,
			addClass:'none',
			showList:[],//商品分类列表
			loadingType: 'more', //more 是加载中   nomore是加载结束
			total:0,//总条数
			pageSize:0,//每页条数
			page:1,//当前页
			nextPage:false,//是否有下一页
			tabList: [{
					state: 0,
					text: '新建分类',
				},
				{
					state: 1,
					text: '管理分类',
				}
			],
			
		};
	},
	
	async onLoad(){
		let userInfo=uni.getStorageSync('userInfo');
		
		if(userInfo!==undefined){
			this.token = userInfo.token;
			this.status = 10;
			this.loadData();
		}else{
			uni.navigateTo({
				url:'/pages/login/login'
			})
		}
		
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		// this.loadData();
	},
	
	methods: {
		//获取商品分类信息
		loadData(){
			let self = this;
			uni.request({
				url: 'http://wxshop.qclike.cn/api/admins/showlist', 
				method:'POST',
				data:{
					token:self.token,
					page:self.page
				},
				success: function(res) {
					if(res.data.code == 1){
						let result = res.data.data;
						if(res.data.code == 1){
							self.showList = result.categorydata;
							
						}
					}
					
				}
			});
			
		}, 
		//禁止手指滑动
		stopTouchMove: function() {
		  return false;
		},

		
		// selectItem(todo){
		// 	console.log(todo);
		// 	let classifyId = todo.id;
		// 	let classifyName = todo.name;
		// 	uni.navigateTo({
		// 		url:`/pages/goods/addgoods?classifyId=${classifyId}&classifyName=${classifyName}`
		// 	})
		// },
		//确认新增分类
		tabConfirm(){
			let self = this;
			if(!self.classification){ 
				uni.showToast({title: '请输入分类名称',icon:"none"});
				return ; 
			}
			self.specClass= 'show';
			self.addClass = 'none';
			uni.request({
				url: 'https://wxshop.qclike.cn/api/admins/cateupdate',
				method:'POST',
				header:{
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{
						token:self.token,
						parent_id:0,
						cate_name:self.classification
						
					},
					success: function(res) {
						if(res.data.code == 1){
							
							self.classification = '';
							
							self.$nextTick(function(){
								self.loadData();
							})
							
						} else if(res.data.code == 0){
							let msg = res.data.msg
							uni.showToast({title: msg,icon:"none"});
						}
				}
			});		
		},
		
		//取消新增分类
		tabCancel(){
			console.log('取消新增分类')
			this.specClass= 'show';
			
			this.addClass = 'none';
		},
		toggleAddClass(){
			if(this.addClass === 'show'){
				this.addClass = 'hide';
				setTimeout(() => {
					this.addClass = 'none';
				}, 250);
			}else if(this.addClass === 'none'){
				this.addClass = 'show';
			}
		},
		//底部tab切换
		tabClick(index){
			let self = this;
			if(index == 0){
				if(self.addClass === 'show'){
					self.addClass = 'hide';
					setTimeout(() => {
						self.addClass = 'none';
					}, 250);
				}else if(self.addClass === 'none'){
					self.addClass = 'show';
				}
			}else if(index == 1){
				uni.navigateTo({
					url:'/pages/goods/manageCategories'
				})
			}
		}
		
		
		
		
	}
}
